.popup__container {
    position: relative;
    width: 430px;
    background: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: 767px){
    .popup__container {
        width: 282px;
    }
}